import { motion } from 'framer-motion';

import '@/components/Hero.css';
import { getHomeInfo } from '@/lib/get_home_info';

const banner = {
  animate: {
    transition: {
      delayChildren: 0.4,
      staggerChildren: 0.1,
    },
  },
};

const letterAnimation = {
  initial: { y: 400 },
  animate: {
    y: 0,
    transition: {
      ease: [0.6, 0.01, -0.05, 0.95],
      duration: 1,
    },
  },
};

export const Hero = async () => {
  const {
    encabezado_1,
    encabezado_2,
    encabezado_3,
    descripcion,
    tituloMultimedia,
    video,
  } = await getHomeInfo();

  return (
    <section className="pt-10 mx-auto relative flex flex-col border-black border-2">
      <div className="border-slate-700 border-2 banner">
        <BannerRowTop title={'brand'} />
        <BannerRowCenter title={'experience'} />
        <BannerRowBottom title={'studio'} />
        {/* <p className="text-9xl font-semibold">{encabezado_1}</p>
        <p className="text-9xl font-semibold left-40">{encabezado_2}</p>
        <p className="text-9xl font-semibold">{encabezado_3}</p> */}
      </div>
      <p className="absolute text-xl top-16 right-24 max-w-[380px]">
        {descripcion}
      </p>

      <video
        className="mt-24 rounded-lg aspect-video w-[840px] self-center"
        controls
      >
        <source src={video} type="video/mp4" className="w-full aspect-video" />
      </video>
    </section>
  );
};

const AnimatedLetters = ({ title, disabled }) => (
  <motion.span
    className="row-title"
    variants={disabled ? null : banner}
    initial="initial"
    animate="animate"
  >
    {[...title].map((letter) => (
      <motion.span
        className="row-letter"
        variants={disabled ? null : letterAnimation}
      >
        {letter}
      </motion.span>
    ))}
  </motion.span>
);

const BannerRowTop = ({ title }) => {
  return (
    <div className={'banner-row'}>
      <div className="row-col">
        <AnimatedLetters title={title} />
      </div>
      <motion.div
        initial={{ opacity: 0, y: 80 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          ease: 'easeInOut',
          duration: 1,
          delay: 0.4,
        }}
        className="row-col"
      >
        <span className="row-message">
          We are specialised in setting up the foundation of your brand and
          setting you up for success.
        </span>
      </motion.div>
    </div>
  );
};

const BannerRowBottom = ({ title }) => {
  return (
    <div className={'banner-row center'}>
      <motion.div
        initial={{ scale: 0 }}
        animate={{ scale: 1 }}
        transition={{ ease: [0.6, 0.01, -0.05, 0.95], duration: 1, delay: 1 }}
        className="scroll"
      >
        <motion.span
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{
            ease: 'easeInOut',
            duration: 1,
            delay: 1.8,
          }}
        >
          scroll
        </motion.span>
        <motion.span
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{
            ease: 'easeInOut',
            duration: 1,
            delay: 1.8,
          }}
        >
          down
        </motion.span>
      </motion.div>
      <AnimatedLetters title={title} />
    </div>
  );
};

const BannerRowCenter = ({ title }) => {
  return (
    <div className={`banner-row marquee`}>
      <motion.div
        initial={{ y: 310 }}
        animate={{ y: 0 }}
        transition={{ ease: [0.6, 0.01, -0.05, 0.9], duration: 1 }}
        className="marquee__inner"
      >
        <AnimatedLetters title={title} disabled />
        <AnimatedLetters title={title} />
        <AnimatedLetters title={title} disabled />
        <AnimatedLetters title={title} disabled />
      </motion.div>
    </div>
  );
};
